import React, { useEffect } from 'react'
import { usePayoutByLinkContext } from './PayoutByLinkProvider'
import Loader from '../../common/loader/Loader'
import PayoutByLinkNewUser from './PayoutByLinkNewUser'
import PayoutByLinkExistingUser from './PayoutByLinkExistingUser'
import { Box } from '@mui/material'
import { getPayoutLinkToken } from '../../api/login/Login'
import { ToastContainer } from 'react-toastify'
const CustomBox =  ({children})=> (
    <Box
    sx={{
      width: '100%',
    //   maxWidth: 800,
      mx: 'auto',
    //   mt: 5,
    my:1,
      px: 3,
      py:4,
      pb:6,
    //   bgcolor: '#F8F9FA',
    bgcolor:'white',
      borderRadius: 1,
      boxShadow: 0,
    }}
  >
    {children}
    </Box>
)

const PayOutByLink = () => {

  const { setToken ,  loader, currentPage , setLoader , setCurrentPage} =     usePayoutByLinkContext()


  const checkUser = async()=>{
     
      try {
        let res = await getPayoutLinkToken()
        if(res?.data?.error === false && res?.data?.statusCode === 200)
        {
            setCurrentPage('existing-user')
            setToken(res?.data?.data)
        }
        else {
            setCurrentPage('new-user') 
        }
      } catch (error) {
        
      } finally {
        setLoader(false)
      }
  }

  useEffect(()=>{
      checkUser()
  },[])
  return (
   <>
             {loader && <Loader />}
              <div className="PageHeading">
        <h1>Payout By Link Service</h1>
      </div>
       { currentPage === 'new-user' &&
    
       <CustomBox>

      
       <PayoutByLinkNewUser/>
        </CustomBox>
        
       }

       {currentPage === 'existing-user' &&
         <CustomBox>
         <PayoutByLinkExistingUser/>
         </CustomBox>
       }
   
   </>
  )
}

export default PayOutByLink