import React from 'react'
import { Box , Button } from '@mui/material'
import { generatePayoutLinkToken } from '../../api/login/Login'
import { usePayoutByLinkContext } from './PayoutByLinkProvider'
import toastMessage from '../../utils/toastMessage'
const PayoutByLinkNewUser = () => {
  const { setToken ,  loader,  setLoader , setCurrentPage} =     usePayoutByLinkContext()
  const geneateToken = async()=>{
     setLoader(true)
      try {
        let res = await generatePayoutLinkToken()
         toastMessage(res?.data?.error , res?.data?.message)
        if(res?.data?.error === false && res?.data?.statusCode === 200)
        {
            setCurrentPage('existing-user')
            setToken(res?.data?.data)
        }
       
     
      } catch (error) {
        
      } finally {
        setLoader(false)
      }
  }
  return (
     <Box
          sx={{
            position: 'relative',
            width: '100%',
            height: 200,
            borderRadius: 2,
            overflow: 'hidden',
            bgcolor: 'grey.100',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            flexDirection: 'column',
            border: '1px solid var(--bs-nav)',
          }}
          
        >
      
          <Button
            variant="outlined"
            sx={{
             
               color: 'var(--bs-nav)',
              borderColor: 'var(--bs-nav)',
              fontSize: 14,
              width:'200px',
              textTransform: 'none',
            }}
            disabled={loader}
            onClick={geneateToken}
           
          >
            Generate Link
            
          </Button>

        </Box>
  )
}

export default PayoutByLinkNewUser